import { Languages, ListItem } from '@limetech/lime-elements';
import translate from '../../global/translations';
import { detectExtension } from './extensionMapping';
import {
    Component,
    Element,
    h,
    Prop,
    State,
    Event,
    EventEmitter,
} from '@stencil/core';
import { Fullscreen } from './fullscreen';
import { FileType, OfficeViewer } from './file-viewer.types';

/**
 * This is a smart component that automatically detects
 * the most common file types such as image, audio, video, and text,
 * and properly displays them in the browser.
 * The component is also capable to render the most common office files.
 *
 * :::note
 * Image files will always be contained in their containers, which means
 * they automatically increases or decreases in size to fill their containing box
 * whilst preserving their aspect-ratio.
 *
 * Text and PDF files will also always respect the width and height of the
 * container in which the `limel-file-viewer` is loaded.
 * :::
 *
 * For some file types such as text and images, the component will display a
 * download button and a button to open the file in a new browser tab.
 * This will allow users to preview the file in a fullscreen more with the
 * browser and take advantage of for example native zooming and panning
 * functionalities.
 *
 *:::important
 * The download button will not work here in this documentation,
 * due to the security policies of the web browsers,
 * since example files are not hosted in the same domain.
 *:::
 * @exampleComponent limel-example-file-viewer
 * @exampleComponent limel-example-file-viewer-office
 * @exampleComponent limel-example-file-viewer-filename
 * @exampleComponent limel-example-file-viewer-actions
 * @exampleComponent limel-example-file-viewer-with-picker
 */

@Component({
    tag: 'limel-file-viewer',
    shadow: true,
    styleUrl: 'file-viewer.scss',
})
export class FileViewer {
    /**
     * Link to the file
     */
    @Prop({ reflect: true })
    public url: string;

    /**
     * The name of the file that also contains its extension.
     * Overrides the filename provided by the URL.
     * Useful when the URL does not contain the filename.
     */
    @Prop({ reflect: true })
    public filename: string;

    /**
     * Alternative text for assistive technologies and screen readers,
     * used for image files.
     */
    @Prop({ reflect: true })
    public alt: string;

    /**
     * Defines the localization for translations.
     */
    @Prop()
    public language: Languages = 'en';

    /**
     * Defines the third-party viewer that should be used to render
     * the content of office files, such as word processing documents,
     * presentations, or spreadsheets.
     */
    @Prop()
    public officeViewer: OfficeViewer = 'microsoft-office';

    /**
     *
     */
    @Prop()
    public actions: ListItem[];

    /**
     *
     */
    @Prop()
    public webserverDomain: string;

    /**
     * determines if the document is hosted on the same domain as the current webserver
     */
    @State()
    public sameDomain: boolean = false;

    /**
     *
     */
    @Event()
    public action: EventEmitter<ListItem>;

    @Element()
    public HostElement: HTMLLimelFileViewerElement;

    private fullscreen: Fullscreen;

    private fileType: FileType;

    constructor() {
        this.fullscreen = new Fullscreen(this.HostElement);
        this.fileType = detectExtension(this.filename, this.url);
    }

    public render() {
        this.sameDomain = this.url.includes(this.webserverDomain);

        if (!this.url) {
            return;
        }

        if (!this.isOfficeFilePubliclyAvailable) {
            return this.renderNoFileSupportMessage();
        }

        switch (this.fileType) {
            case 'pdf':
                return this.renderPdf();
            case 'image':
                return this.renderImage();
            case 'video':
                return this.renderVideo();
            case 'audio':
                return this.renderAudio();
            case 'text':
                return this.renderText();
            case 'office':
                return this.renderOffice();
            default:
                return this.renderNoFileSupportMessage();
        }
    }

    private isOfficeFilePubliclyAvailable() {
        return (
            this.fileType === 'office' &&
            !(this.url.startsWith('http://') || this.url.startsWith('https://'))
        );
    }

    private getOfficeViewerUrl() {
        if (this.officeViewer === 'microsoft-office') {
            return 'https://view.officeapps.live.com/op/embed.aspx?src=';
        }

        if (this.officeViewer === 'google-drive') {
            return 'https://docs.google.com/gview?url=';
        }

        if (this.officeViewer === 'microsoft-sharepoint') {
            return;
            // <iframe src="https://limetechab.sharepoint.com/sites/ProductManagement/_layouts/15/Doc.aspx?sourcedoc={89338338-e9e5-43e4-84ce-b3c32d286a1f}&amp;action=embedview" />
        }
    }

    private renderOffice() {
        // the button needs more styling, it is currently sitting beside the iframe
        const buttonStyle = {
            position: 'relative',
            float: 'right',
            right: '10px',
            bottom: '35px',
            transition: '0.5s',
        };

        if (this.sameDomain) {
            return [
                <a href={this.url} style={buttonStyle}>
                    download
                </a>,
                <div class="action-menu-for-office-files">
                    {this.renderActionMenu()}
                </div>,
                <iframe
                    src={
                        this.getOfficeViewerUrl() + this.url + '&embedded=true'
                    }
                    frameborder="0"
                ></iframe>,
            ];
        }

        return [
            <div class="action-menu-for-office-files">
                {this.renderActionMenu()}
            </div>,
            <iframe
                src={this.getOfficeViewerUrl() + this.url + '&embedded=true'}
                frameborder="0"
            ></iframe>,
        ];
    }

    private renderText() {
        return [
            this.renderButtons(),
            <object data={this.url} type="text/plain"></object>,
        ];
    }

    private renderPdf() {
        return <object data={this.url} type="application/pdf"></object>;
    }

    private renderImage() {
        return [this.renderButtons(), <img src={this.url} alt={this.alt} />];
    }

    private renderAudio() {
        return (
            <audio controls>
                <source src={this.url} />
            </audio>
        );
    }

    private renderVideo() {
        return (
            <video controls>
                <source src={this.url} />
            </video>
        );
    }

    private renderNoFileSupportMessage() {
        return (
            <div class="no-support">
                <div class="no-support__info">
                    <limel-icon
                        class="icon--warning"
                        name="brake_warning"
                        size="large"
                    ></limel-icon>
                    <div>
                        {this.getTranslation('message.unsupported-filetype')}
                    </div>
                </div>
                <a
                    href={this.url}
                    title="download"
                    class="button--download"
                    role="button"
                    download
                >
                    <limel-icon name="download_2" size="large" />
                </a>
            </div>
        );
    }

    private renderButtons() {
        return (
            <div class="buttons">
                {this.renderActionMenu()}
                {this.renderFullscreenButtons()}
                <a
                    href={this.url}
                    title={this.getTranslation('title.download')}
                    class="button--download"
                    role="button"
                    download={this.filename}
                    target="_blank"
                >
                    <limel-icon name="download_2" size="small" />
                </a>
                <a
                    class="button--new-tab"
                    role="button"
                    href={this.url}
                    target="_blank"
                    rel="noopener noreferrer"
                    title={this.getTranslation('title.open-in-new-tab')}
                >
                    <limel-icon name="external_link" size="small" />
                </a>
            </div>
        );
    }

    private renderFullscreenButtons() {
        if (this.fullscreen.isSupported()) {
            return [
                <a
                    class="button--exit-fullscreen"
                    role="button"
                    onClick={this.fullscreen.toggle}
                    title={this.getTranslation('title.exit-fullscreen')}
                >
                    <limel-icon name="multiply" size="small" />
                </a>,
                <a
                    class="button--enter-fullscreen"
                    role="button"
                    onClick={this.fullscreen.toggle}
                    title={this.getTranslation('title.open-in-fullscreen')}
                >
                    <limel-icon name="fit_to_width" size="small" />
                </a>,
            ];
        }
    }

    private renderActionMenu() {
        if (!this.actions) {
            return;
        }

        return (
            <limel-menu
                class="action-menu"
                items={this.actions}
                // onSelect={this.emitOnAction}
                open-direction="left"
            >
                <a title="More..." class="button--action" slot="trigger">
                    <limel-icon name="menu_2" size="small" />
                </a>
            </limel-menu>
        );
    }

    private emitOnAction = (event: CustomEvent<ListItem>) => {
        event.stopPropagation();
        this.action.emit(event.detail);
    };

    private getTranslation(key: string) {
        return translate.get(`file-viewer.${key}`, this.language);
    }
}
