/* eslint-disable @stencil/dependency-suggestions */

export function detectExtension(fileName, url): any {
    const pathLike = fileName || url;
    let extension = '';
    let type = '';

    if (!pathLike) {
        extension = '';
        type = 'unknown';

        return;
    }

    extension = pathLike.split('.').pop().toLowerCase();
    switch (extension) {
        case 'pdf':
            type = 'pdf';
            break;
        case 'jpg':
        case 'jpeg':
        case 'heic':
        case 'bmp':
        case 'png':
        case 'gif':
        case 'svg':
        case 'svgz':
        case 'ep':
        case 'eps':
            type = 'image';
            break;
        case 'avi':
        case 'flv':
        case 'h264':
        case 'mov':
        case 'mp4':
        case 'mwv':
        case 'mkv':
            type = 'video';
            break;
        case 'mp3':
        case 'wav':
        case 'wma':
        case 'ogg':
            type = 'audio';
            break;
        case 'txt':
        case 'json':
        case 'html':
        case 'xml':
            type = 'text';
            break;
        case 'doc':
        case 'docx':
        case 'ppt':
        case 'xls':
        case 'xlsx':
        case 'odt':
        case 'ods':
        case 'odp':
        case 'dot':
        case 'dotx':
        case 'docm': // not supported https://example-files.online-convert.com/document/docm/example.docm
        case 'dotm': // not yet tested
        case 'pages': // not supported https://example-files.online-convert.com/document/pages/example.pages
        case 'pptx':
        case 'csv': // not supported https://file-examples-com.github.io/uploads/2017/02/file_example_CSV_5000.csv
        case 'numbers': // not supported https://example-files.online-convert.com/spreadsheet/numbers/example.numbers
        case 'pot': // not tested
        case 'pps':
        case 'ppsx':
        case 'pptm': // not supported https://israeltekahun.com/pptm.pptm
        case 'potx': // not supported https://israeltekahun.com/potx.potx
        case 'potm': // not supported https://israeltekahun.com/potm.potm
        case 'ppam': // could not find file
        case 'ppsm': // not supported https://israeltekahun.com/ppsm.ppsm
        case 'sldx': // not tested
        case 'sldm': // not tested
        case 'key': // not tested
            type = 'office';
            break;
        default:
            type = 'unknown';
            break;
    }

    return type;
}
